<template>
  <div>
    <v-container class="admin-usuarios__container">
      <v-row>
        <v-col>
          <show-dialog-usuario
            v-bind="{ value }"
            @procesar="procesar($event)"
            @cerrar="cerrar()"
          />
          <base-material-card
            color="success"
            icon="mdi-view-carousel"
            inline
            title="usuarios"
            class="px-5 py-3 mb-5"
          >
            <v-row class="admin-usuarios_row__nuevo">
              <v-col class="text-right">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="success ma-0"
                      v-bind="attrs"
                      @click="abrirCrear()"
                      v-on="on"
                    >
                      Nuevo Usuario
                    </v-btn>
                  </template>
                  <span>Crea un nuevo Usuario</span>
                </v-tooltip>
              </v-col>
            </v-row>
            <v-simple-table class="admin-usuarios__table">
              <thead>
                <tr>
                  <th>Codigo</th>
                  <th>Email</th>
                  <th>Tipo</th>
                  <th style="text-align:right;">
                    Acciones
                  </th>
                </tr>
              </thead>

              <tbody>
                <tr
                  v-for="(item, i) in usuarios"
                  :key="i"
                >
                  <td>{{ item.CodUsuario }}</td>
                  <td>{{ item.Nombre }}</td>
                  <td>{{ item.TipoUsuario }}</td>
                  <td class="text-right">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="warning btn-edit"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="abrirEditar(item.CodUsuario)"
                        >
                          <v-icon>mdi-pencil</v-icon>
                        </v-btn>
                      </template>
                      <span>Editar</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="error btn-delete"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="eliminar(item.CodUsuario, item.Nombre)"
                        >
                          <v-icon> mdi-trash-can </v-icon>
                        </v-btn>
                      </template>
                      <span>Eliminar</span>
                    </v-tooltip>
                  </td>
                </tr>
              </tbody>
            </v-simple-table>
          </base-material-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { mapState, mapMutations } from 'vuex'
  import swalAlertError from '@/mixins'
  export default {
    components: {
      ShowDialogUsuario: () => import('@/components/base/ShowDialogUsuario'),
    },
    mixins: [swalAlertError],
    data () {
      return {
        value: {
          active: false,
          usuario: {
            CodUsuario: '',
            Nombre: '',
            Email: '',
            Password: '',
            Sexo: '',
            TipoUsuario: '',
          },
          imagen: null,
          loader: false,
          usuariosTipos: [],
        },
        usuarios: '',
      }
    },
    computed: {
      ...mapState(['token']),
    },
    created () {
      this.getUsuarios()
      this.getUsuariosTipos()
    },
    methods: {
      ...mapMutations(['cerrarSesion']),
      getUsuarios () {
        Vue.axios
          .get('/api/usuarios')
          .then(res => {
            this.usuarios = res.data.result
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      getUsuariosTipos () {
        Vue.axios
          .get('/api/usuarios/tipos')
          .then(res => {
            this.value.usuariosTipos = res.data.result
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      getUsuario (codUsuario) {
        Vue.axios
          .get(`/api/usuarios/${codUsuario}`)
          .then(res => {
            this.value.usuario = res.data.result[0]
            this.value.usuario.Sexo = Number(this.value.usuario.Sexo)
            this.value.usuario.Password = ''
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      abrirEditar (codUsuario) {
        this.getUsuario(codUsuario)
        this.value.active = true
      },
      abrirCrear () {
        this.value.active = true
      },
      cerrar () {
        this.value.active = false
        this.value.usuario = {}
        this.value.imagen = null
        this.value.loader = false
      },
      procesar (val) {
        console.log('procesando...')
        this.value.loader = true
        const headers = {
          'x-token': this.token,
        }
        const body = new FormData()
        body.append('codUsuario', val.usuario.CodUsuario)
        body.append('nombre', val.usuario.Nombre)
        body.append('email', val.usuario.Email)
        body.append('password', val.usuario.Password)
        body.append('tipoUsuario', val.usuario.TipoUsuario)
        body.append('sexo', val.usuario.Sexo)
        body.append('imagen', val.imagen)

        if (val.usuario.CodUsuario) {
          this.update(body, headers)
        } else {
          this.create(body, headers)
        }
      },
      create (body, headers) {
        Vue.axios
          .post('/api/usuarios', body, { headers })
          .then(res => {
            this.usuarios.unshift(res.data.usuario[0])
            this.cerrar()
            Vue.swal('Usuario Creado', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      update (body, headers) {
        console.log('editando')
        Vue.axios
          .put('/api/usuarios', body, { headers })
          .then(res => {
            const index = this.usuarios.findIndex(
              usuario =>
                usuario.CodUsuario === Number(res.data.usuario[0].CodUsuario),
            )
            this.usuarios[index].Nombre = res.data.usuario[0].Nombre
            this.usuarios[index].TipoUsuario = res.data.usuario[0].TipoUsuario
            this.cerrar()
            Vue.swal('Usuario Actualizado', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      eliminar (CodUsuario, Nombre) {
        Vue.swal({
          title: `Estás seguro que deseas borrar a ${Nombre}?`,
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText: 'Eliminar',
          cancelButtonText: 'Cancelar',
          confirmButtonColor: '#ff5252',
          cancelButtonColor: '#a1a1a1',
        }).then(result => {
          if (result.isConfirmed) {
            Vue.swal('Usuario Eliminado', '', 'success')
            Vue.axios
              .delete(`/api/usuarios/${CodUsuario}`, {
                headers: { 'x-token': this.token },
              })
              .then(res => {
                const index = this.usuarios.findIndex(
                  u => u.CodUsuario === Number(res.data.codUsuario),
                )
                this.usuarios.splice(index, 1)
              })
              .catch(e => {
                this.cerrar()
                this.swalAlertError(e)
              })
          }
        })
      },
    },
  }
</script>
